* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-size: 16px;
  font-family: "Helvetica Neue", sans-serif;
}

/* CAlculator */
.calculator-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1200px;
  margin: 10px auto;
  background-color: #efefef;
  padding: 40px;
  border-radius: 10px;
  border: 1px solid #bfbfbf;
}
.calculator-wrapper section {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
	text-align: left;
}
.calculator-wrapper section p {
	margin-bottom: 10px;
}
.calculator-header {
  font-size: 30px;
  margin-bottom: 20px;
}
.calculator-container {
  width: 100%;
  display: flex;
  margin-bottom: 20px;
}
.transport-type ul li {
  display: inline;
  margin-right: 10px;
  padding: 5px 15px;
  cursor: pointer;
  border-radius: 14px;
}
.transport-type .active {
  background-color: red;
  color: white;
}
.calculator-wrapper .total-price {
  font-weight: 800;
  text-align: left;
  width: 100%;
  font-size: 24px;
}
.calculator-wrapper .dop-factors input {
  display: none;
}
.calculator-wrapper .dop-factors label {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}
.calculator-wrapper .dop-factors label:before {
  content: "";
  width: 30px;
  height: 30px;
  display: flex;
  margin-right: 10px;
  background-color: #fff;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  border: 2px solid #bbb;
}
.calculator-wrapper .dop-factors input:checked + label:before {
  content: "✔︎";
  color: #fff;
  background-color: red;
  border-color: red;
}
.calculator-wrapper .distance {
  display: flex;
  flex-direction: column;
}
.calculator-wrapper .distance label {
  font-size: 20px;
  font-weight: 800;
}
.calculator-wrapper .distance input {
  -webkit-appearance: none;
  appearance: none;
  background: white;
  cursor: pointer;
  width: 15rem;
  width: 100%;
  border: none;
  border-radius: 8px;
  height: 20px;
  margin: 20px 0;
  border: 1px solid #bbb;
}
.calculator-wrapper .distance input::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  margin-top: -12px; /* Centers thumb on the track */
  background-color: red;
  height: 30px;
  width: 15px;
  transform: translateY(5px);
  border-radius: 15px;
}
.blocked-wheels {
  display: flex;
  width: 100%;
}

.blocked-wheels button {
  width: 40px;
  height: 40px;
}
.blocked-wheels input {
  height: 40px;
  text-align: center;
  font-size: 20px;
  padding: 5px 10px;
}
.blocked-wheels input::-webkit-outer-spin-button,
.blocked-wheels input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* CONTACT FORM */
.contact-form-wrapper {
  display: flex;
  width: 100%;
  max-width: 1200px;
  padding: 40px;
  align-items: center;
  margin: 10px auto;
  background-color: #000;
  border-radius: 10px;
  border: 1px solid #bfbfbf;
  color: #fff;
  gap: 20px;
}
.contact-form-wrapper div {
  width: 100%;
}
.contact-form-wrapper h2 {
  font-size: 24px;
  margin-bottom: 20px;
}
.contact-form-wrapper input {
  width: 100%;
  border: none;
  outline: none;
  height: 30px;
  margin-bottom: 20px;
}

.contact-form-wrapper .btn {
  background-color: red;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  cursor: pointer;
  padding: 10px;
  border-radius: 8px;
}
.politika-wrapper {
  display: flex;
  width: 100%;
	flex-direction: column;
	align-items: flex-start;
}
.contact-form-wrapper input {
	padding: 5px 10px;
	border-radius: 4px;
}
.politika-wrapper .error {
	color: red;
  font-size: 16px;
	margin-bottom: 20px;
}
.politika-wrapper input {
  display: none;
}
.politika-wrapper label:before {
  content: "";
  width: 30px;
  height: 30px;
  display: flex;
  margin-right: 10px;
  background-color: #fff;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  border: 2px solid #bbb;
}
.politika-wrapper label {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  text-align: left;
}
.politika-wrapper input:checked + label:before {
  content: "✔︎";
  color: #fff;
  background-color: red;
  border-color: red;
}
.politika-wrapper a {
  color: #fff;
}
.contact-form-wrapper input::-webkit-outer-spin-button,
.contact-form-wrapper input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
